<template>
  <div id="comboMg">
    <el-card class="comboMg-con">
      <el-button
        @click="addPackage"
        size="small"
        type="primary"
        class="create-package"
        ></el-button
      >
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="1"></el-tab-pane>
        <el-tab-pane label="" name="2"></el-tab-pane>
      </el-tabs>

      <el-row v-show="activeName == '1'">
        <el-table :data="table">
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <div>: {{ scope.row.shopNum }}</div>
              <div>: {{ scope.row.orderNum }}</div>
              <div>: {{ scope.row.subUserCnt }}</div>
              <div>spu: {{ scope.row.productCnt }}</div>
            </template>
          </el-table-column>
          <el-table-column prop="price" label="">
            <template slot-scope="scope">
              {{ scope.row.amount }}/
            </template>
          </el-table-column>
          <el-table-column prop="count" label=""></el-table-column>
          <el-table-column prop="" label="">
            <template slot-scope="scope">
              <el-link @click="editPackage(scope.row)"></el-link>
              <el-link
                class="lf-ma"
                v-if="scope.row.name != '' && scope.row.count == 0"
                type="danger"
                @click="delPackage(scope.row)"
                ></el-link
              >
            </template>
          </el-table-column>
        </el-table>
      </el-row>

      <el-row v-show="activeName == '2'">
        <el-table :data="items2">
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="desc" label=""></el-table-column>
          <el-table-column prop="price" label=""></el-table-column>
          <el-table-column prop="count" label=""></el-table-column>
        </el-table>
      </el-row>
    </el-card>
    <CreatePackageDialog ref="createPackageDialog" />
  </div>
</template>

<script>
import CreatePackageDialog from "./CreatePackageDialog.vue";

export default {
  components: { CreatePackageDialog },
  data() {
    return {
      activeName: "1",
      table: [],
      items2: [
        {
          name: "",
          desc: "Shopify",
          price: "150000",
          count: 0,
        },
        {
          name: "",
          desc: "",
          price: "10/1000",
          count: 0,
        },
        {
          name: "",
          desc: "",
          price: "100/1/10000",
          count: 0,
        },
        {
          name: "",
          desc: "",
          price: "5/1/10000",
          count: 0,
        },
      ],
      serviceCount: 0,
      mailCount: 0,
    };
  },
  mounted() {
    this.getItems();
  },
  methods: {
    handleClick(val) {
      if (this.activeName == 1) {
      }
    },
    getItems() {
      this.$apiCall("api.Admin.vipSummary", {}, (r) => {
        if (r.ErrorCode == 9999) {
          this.items2[0].count = r.Data.Results.serviceCnt;
          this.items2[1].count = r.Data.Results.mailCnt;
          this.items2[2].count = r.Data.Results.productCnt;
          this.items2[3].count = r.Data.Results.subUserCnt;
          this.table = r.Data.Results.vipInfo;
        }
      });
    },
    addPackage() {
      this.$refs.createPackageDialog.openDialog({ data: "", type: 1 });
    },
    editPackage(data) {
      this.$refs.createPackageDialog.openDialog({ data: data, type: 2 });
    },
    delPackage(row) {
      let params = {
        id: row.id,
      };
      this.$apiCall("api.VendorVip.delete", params, (r) => {
        if (r.ErrorCode == 9999) {
          this.$message({ message: "", type: "success" });
          this.getItems();
        } else {
          this.$message({ message: r.Message, type: "error" });
        }
      });
    },
  },
};
</script>

<style scoped>
.lf-ma {
  margin-left: 10px;
}
.comboMg-con {
  position: relative;
}
.create-package {
  position: absolute;
  right: 20px;
  top: 15px;
  z-index: 999;
}
</style>